        <h2>What is PacketLab?</h2>
        <p>PacketLab is a universal measurement endpoint interface that lowers the barriers faced by experimenters and measurement endpoint operators. PacketLab is designed to complement existing network measurement interfaces rather than replace them entirely.</p> 
        <p>PacketLab is based on two key ideas:</p>
        <ol>
            <li><u>Moving measurement logic out of the endpoint to a separate experiment control server, making each endpoint a lightweight packet source/sink</u></li>
            <li><u>Providing a way to delegate access to measurement endpoints while retaining fine-grained control over how endpoints are used</u></li>
        </ol>
        <p>PacketLab is currently still under development, and many additions are underway. The current build of PacketLab (which can be downloaded at the top of this page) is suitable for creating and running experiments using an Experiment Controller and a Measurement Endpoint. A Broker and Proxy are currently in the works, as well as support for additional experiments.</p>

        <!-- Network experiments have never been this easy -->

        <h2>How is PacketLab used?</h2>

        <p>PacketLab makes <b>running network experiments</b> easier by connecting experimenters to measurement endpoints. What makes PacketLab powerful is its flexibility. Experimenters have control over the experiments they want to use PacketLab resources to run, and PacketLab supports the running of custom experiments.</p>
        <p>PacketLab is also meant for experimenters interested in <b>writing network experiments</b> and exploring new frontiers of computer networking. The PacketLab library contains tools that can come in handy for many different types of experiments. The library's functions and objects make experiment-writing simpler, and help experiments run seamlessly with PacketLab endpoints and controllers.</p>
        <p>However, <b>hosting experiments</b> is just as vital as writing them, and PacketLab is also made for endpoint operators. PacketLab endpoint operators can make their endpoints completely private, completely public, or anywhere in between. By volunteering your server as a public PacketLab endpoint, you enable experimenters to make discoveries through PacketLab and help advance computer networking knowledge.</p> 
        <p>PacketLab can also be used for <b>managing endpoints</b> and handling the sharing of measurement resources. Operators can restrict what experiments experimenters can run based on type or resources needed. This can come in handy for a small group using private endpoints for internal use, as well as for people hosting public endpoints that want to manage how much their endpoints are used and who is able to use them.</p>

        <h2>Who made PacketLab?</h2>
        <p>PacketLab is currently being developed by a collaboration of students and researchers from the Center for Applied Data Analysis (<a href="caida.org">CAIDA</a>), the University of California at San-Diego (<a href="ucsd.edu">UCSD</a>), and the University of Illinois at Urbana-Champaign (<a href="illinois.edu">UIUC</a>). The team consists of professional research scientists, professors, graduate students, and even some undergrads! Check out <router-link :to="{name:'Our Team'}" v-slot="{route,href}"><strong><a :href="href" :class="{'active':currentRouteName==route.name}">OUR TEAM</a></strong></router-link> of researchers and students.</p>
        <p><a href="https://www.caida.org/publications/papers/2017/packetlab/packetlab.pdf" target="_blank">See the paper that started PacketLab</a>.</p>

        <h2>Host an Endpoint!</h2>
        <p>Currently, PacketLab is not yet ready for endpoint operators to host public endpoints for PacketLab. However, we expect to be ready to welcome endpoint hosts very soon!</p>

